html,
body {
  color: #000;
  width: 100%;
  overflow: hidden;
  height: 100%;
  padding: 0;
}
html *,
body * {
  cursor: default;
}
html .channgepwd-container,
body .channgepwd-container {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
html .fixed,
body .fixed {
  position: fixed !important;
}
html .fixed .name,
body .fixed .name {
  position: absolute;
  width: 100%;
  height: 0.5rem !important;
  line-height: 0.5rem;
  font-size: 0.18rem;
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
  bottom: 0;
  left: 0;
  text-indent: 0.14rem;
}
html button,
body button {
  -webkit-appearance: none;
  border: 0;
  margin: 0;
  padding: 0;
  box-shadow: none;
  outline: none;
}
html button:active,
body button:active {
  opacity: 0.6;
}
html .page,
body .page {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
html .page::-webkit-scrollbar,
body .page::-webkit-scrollbar {
  display: none;
}
html .page .logout-btn,
body .page .logout-btn {
  position: absolute;
  z-index: 10;
  left: 0.2rem;
  top: 0.2rem;
  color: #fff;
  font-size: 0.3rem;
  padding: 0.1rem;
  opacity: 0;
}
html .page .icon-img-box,
body .page .icon-img-box {
  width: 0.17rem;
  height: 0.17rem;
  position: absolute;
  top: 0.17rem;
  right: 0.17rem;
}
html .page .icon-img,
body .page .icon-img {
  width: 0.17rem;
  height: 0.17rem;
}
html .page.changepwd-page,
body .page.changepwd-page {
  background: url(./b0c72319baa260e552f3b4c1d7f2d4c9.png) no-repeat center;
  background-size: auto 100%;
}
html .page.changepwd-page .changepwd-box,
body .page.changepwd-page .changepwd-box {
  background: #fff;
  border-radius: 0.33rem;
  width: 4.44rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.3rem 0;
  box-sizing: border-box;
}
html .page.changepwd-page .changepwd-box .title,
body .page.changepwd-page .changepwd-box .title {
  width: 3.16rem;
  height: 0.34rem;
  margin: 0 auto 0.3rem;
  font-size: 0.22rem;
  font-weight: 600;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-bottom: 0.02rem solid #eee;
}
html .page.changepwd-page .changepwd-box .title .box-mline,
body .page.changepwd-page .changepwd-box .title .box-mline {
  width: 0.01rem;
  height: 0.2rem;
  border-left: 0.01rem solid #eee;
}
html .page.changepwd-page .changepwd-box .title .none-color,
body .page.changepwd-page .changepwd-box .title .none-color {
  height: 0.34rem;
  border-bottom: 0.03rem solid #fbab22;
  color: #fbab22;
}
html .page.changepwd-page .changepwd-box .title .current-style,
body .page.changepwd-page .changepwd-box .title .current-style {
  height: 0.34rem;
  color: #3882c2;
  border-bottom: 0.02rem solid #3882c2;
}
html .page.changepwd-page .changepwd-box .input-control,
body .page.changepwd-page .changepwd-box .input-control {
  margin-bottom: 0.22rem;
}
html .page.changepwd-page .changepwd-box .input-control .input-box,
body .page.changepwd-page .changepwd-box .input-control .input-box {
  height: 0.55rem;
  display: flex;
  align-items: center;
  width: 3.16rem;
  border-radius: 0.3rem;
  background: #edf5ff;
  padding: 0 0.02rem;
  box-sizing: border-box;
}
html .page.changepwd-page .changepwd-box .input-control .input-box input,
body .page.changepwd-page .changepwd-box .input-control .input-box input {
  border: 0;
  outline: none;
  margin: 0 0.14rem;
  height: 0.2rem;
  flex: 1;
  font-size: 0.16rem;
  color: #333;
  border-left: 0.01rem solid #999;
  padding-left: 0.1rem;
  box-sizing: border-box;
  background: #edf5ff;
}
html .page.changepwd-page .changepwd-box .input-control .input-box .newpwd-input,
body .page.changepwd-page .changepwd-box .input-control .input-box .newpwd-input {
  width: 2rem!important;
  border: 0;
  margin: 0;
}
html .page.changepwd-page .changepwd-box .input-control .input-box .icon-img,
body .page.changepwd-page .changepwd-box .input-control .input-box .icon-img {
  margin-left: 0.14rem;
  width: 0.2rem;
  height: 0.26rem;
}
html .page.changepwd-page .changepwd-box .code-inputbox,
body .page.changepwd-page .changepwd-box .code-inputbox {
  display: flex;
  justify-content: space-between;
  border: 0!important;
  background: #fff !important;
}
html .page.changepwd-page .changepwd-box .code-inputbox .input-left-box,
body .page.changepwd-page .changepwd-box .code-inputbox .input-left-box {
  width: 2.02rem !important;
  height: 0.52rem;
  display: flex;
  align-items: center;
  border-radius: 0.35rem;
  background: #edf5ff;
}
html .page.changepwd-page .changepwd-box .code-inputbox .input-left-box input,
body .page.changepwd-page .changepwd-box .code-inputbox .input-left-box input {
  width: 80%;
  color: #333;
}
html .page.changepwd-page .changepwd-box .code-inputbox .code-box,
body .page.changepwd-page .changepwd-box .code-inputbox .code-box {
  width: 33%;
  height: 0.5rem;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.06rem;
  background: url(./343a151ea31f14cbe2eeae245abf3a01.png) no-repeat;
  background-size: 100% 100%;
  font-size: 0.17rem;
  color: #fff;
}
html .page.changepwd-page .changepwd-box .code-inputbox .open-code,
body .page.changepwd-page .changepwd-box .code-inputbox .open-code {
  color: #a66900;
  font-size: 0.17rem;
  background: url(./5ddfd8a66467e4a150b22f055428c1d3.png) no-repeat;
  background-size: 100% 100%;
}
html .page.changepwd-page .changepwd-box .login-btn,
body .page.changepwd-page .changepwd-box .login-btn {
  width: 2.4rem;
  height: 0.6rem;
  background: url(./d77f64e83603f37f5b4632d31857bbde.png) no-repeat;
  background-size: 100% 100%;
  display: block;
  margin: 0 auto;
  margin-top: 0.2rem;
}
html .page.changepwd-page .changepwd-box .login-btn:active,
body .page.changepwd-page .changepwd-box .login-btn:active {
  opacity: 1;
}
html .page.changepwd-page .changepwd-box .login-btn[disabled],
body .page.changepwd-page .changepwd-box .login-btn[disabled] {
  opacity: 0.6;
}
html .page.changepwd-page .changepwd-box .not-btn,
body .page.changepwd-page .changepwd-box .not-btn {
  width: 2.4rem;
  height: 0.57rem;
  display: block;
  margin: 0 auto;
  margin-top: 0.2rem;
  background: url(./9e51c182938c8086d0be13ed39db6be9.png) no-repeat;
  background-size: 100% 100%;
}
html .page.changepwd-page .changepwd-box .not-btn:active,
body .page.changepwd-page .changepwd-box .not-btn:active {
  opacity: 1;
}
html .page.changepwd-page .changepwd-box .not-btn[disabled],
body .page.changepwd-page .changepwd-box .not-btn[disabled] {
  opacity: 0.6;
}
html .page.changepwd-page .changepwd-box.with-dentity,
body .page.changepwd-page .changepwd-box.with-dentity {
  height: 3.6rem;
}
html .page.changepwd-page .changepwd-box .login-dentity,
body .page.changepwd-page .changepwd-box .login-dentity {
  width: 3rem;
}
html .page.changepwd-page .changepwd-box .login-radio,
body .page.changepwd-page .changepwd-box .login-radio {
  width: 3.16rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 0.22rem;
  font-size: 0.16rem;
}
html .page.changepwd-page .changepwd-box .login-radio label,
body .page.changepwd-page .changepwd-box .login-radio label {
  margin-right: 0.2rem;
  display: flex;
  justify-content: flex-start;
}
html .slave,
body .slave {
  position: fixed;
  width: 0.1rem;
  height: 0.1rem;
  background: rgba(0, 0, 0, 0);
  z-index: 10000;
  right: 0;
  top: 0;
}
html .safe-mask,
body .safe-mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}
html .safe-mask .safe-container,
body .safe-mask .safe-container {
  width: 4.42rem;
  padding: 0rem 0.32rem 0.3rem 0.32rem;
  box-sizing: border-box;
  background: #fff;
  border-bottom: 0.02rem solid #3882c2;
  position: relative;
  border-radius: 0.2rem;
}
html .safe-mask .safe-container .icon-img-box,
body .safe-mask .safe-container .icon-img-box {
  width: 0.17rem;
  height: 0.17rem;
  position: absolute;
  top: 0.17rem;
  right: 0.17rem;
}
html .safe-mask .safe-container .icon-img,
body .safe-mask .safe-container .icon-img {
  width: 0.17rem;
  height: 0.17rem;
}
html .safe-mask .safe-container .safe-title,
body .safe-mask .safe-container .safe-title {
  width: 100%;
  text-align: center;
  font-size: 0.22rem;
  color: #fbab22;
  margin-top: 0.3rem;
  font-weight: bold;
}
html .safe-mask .safe-container .pic-codewrap,
body .safe-mask .safe-container .pic-codewrap {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 0.34rem;
}
html .safe-mask .safe-container .pic-codewrap .input-left-box,
body .safe-mask .safe-container .pic-codewrap .input-left-box {
  width: 2.6rem;
  height: 0.55rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #edf5ff;
  border-radius: 0.32rem;
  padding: 0 0.2rem;
  box-sizing: border-box;
}
html .safe-mask .safe-container .pic-codewrap .input-left-box .icon-img,
body .safe-mask .safe-container .pic-codewrap .input-left-box .icon-img {
  width: 0.2rem;
  height: 0.24rem;
}
html .safe-mask .safe-container .pic-codewrap .input-left-box input,
body .safe-mask .safe-container .pic-codewrap .input-left-box input {
  flex: 1;
  outline: none;
  border: none;
  background-color: #edf5ff;
  border-left: 0.01rem solid #999;
  font-size: 0.16rem;
  color: #999;
  padding: 0 0.05rem;
  box-sizing: border-box;
  margin-left: 0.1rem;
}
html .safe-mask .safe-container .pic-codewrap .pic-codebox,
body .safe-mask .safe-container .pic-codewrap .pic-codebox {
  width: 1rem;
  height: 0.5rem;
  background-color: #fafafa;
  border-radius: 0.06rem;
  border: solid 0.01rem #b7b7b7;
}
html .safe-mask .safe-container .pic-codewrap .pic-codebox .icon-img,
body .safe-mask .safe-container .pic-codewrap .pic-codebox .icon-img {
  width: 100%;
  height: 100%;
  border-radius: 0.06rem;
}
html .safe-mask .safe-container .login-btn,
body .safe-mask .safe-container .login-btn {
  width: 2.4rem;
  height: 0.6rem;
  background: url(./036e7493049bde799b4ba7e7114ea67a.png) no-repeat;
  background-size: 100% 100%;
  display: block;
  margin: 0 auto;
  margin-top: 0.3rem;
}
