.helper-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.helper-block .code {
  margin-top: -0.2rem;
  width: 2.2rem;
  height: 2.2rem;
  margin-bottom: 0.1rem;
}
.helper-block .code img {
  width: 100%;
  height: 100%;
}
.helper-block .name {
  width: 1.7rem;
  height: 0.4rem;
  background-color: #fe6969;
  border-radius: 0.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.3rem;
  color: #ffffff;
  margin-bottom: 0.4rem;
}
.helper-block .tips {
  font-size: 0.2rem;
  color: #999;
  margin-top: -0.1rem;
}
.helper-block .tips.sunlogin {
  margin-top: 0.01rem;
  color: #8f8f8f;
  font-size: 0.14rem;
}
.helper-block .tips.sunlogin:hover {
  color: #bfbfbf;
}
