.view-user {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.view-user.teacher .nickname {
  border: 0;
  text-align: center;
}
.view-user .avatar {
  width: 1rem;
  height: 1rem;
  border: 0.03rem solid #fdd24f;
  border-radius: 50%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.view-user .avatar input {
  width: 100%;
  height: 100%;
  opacity: 0;
}
.view-user .nickname {
  margin-top: 0.14rem;
  margin-bottom: 0.36rem;
  width: 3.3rem;
  height: 0.55rem;
  background-color: #edf5ff;
  border-radius: 0.3rem;
  padding-left: 0.2rem;
  padding-right: 0.1rem;
  box-sizing: border-box;
  font-size: 0.2rem;
  color: #333333;
  display: flex;
  font-weight: 600;
  align-items: center;
}
.view-user .nickname .edit-btn {
  width: 0.7rem;
  height: 0.4rem;
  background: url(./0d981607d7645bcba2fa70777ba19aa5.png) no-repeat center center;
  background-size: 100% 100%;
}
.view-user .nickname .text {
  flex: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.view-user .nickname input {
  width: 1.54rem;
  border: 0;
  height: 0.3rem;
  line-height: 0.3rem;
  color: #333333;
  font-size: 0.2rem;
  outline: none;
  background-color: #edf5ff;
}
.view-user .logout-btn {
  position: absolute;
  font-size: 0.19rem;
  color: #666;
  left: 0.2rem;
  bottom: 0.16rem;
  text-decoration: none;
}
.view-user .logout-btn:active {
  opacity: 0.8;
}
.view-user .logout-line {
  position: absolute;
  color: #666;
  left: 1.05rem;
  bottom: 0.28rem;
  width: 0.01rem;
  height: 0.15rem;
  background: #666;
  z-index: 333;
}
.view-user .change-pw {
  position: absolute;
  font-size: 0.19rem;
  color: #666;
  left: 1.12rem;
  bottom: 0.16rem;
  text-decoration: none;
}
.view-user .change-pw:active {
  opacity: 0.8;
}
.view-user .version {
  font-size: 0.17rem;
  color: #b4b4b4;
  position: absolute;
  right: 1.05rem;
  bottom: 0.18rem;
}
.view-user .cleardata {
  font-size: 0.17rem;
  color: #b4b4b4;
  position: absolute;
  right: 0.2rem;
  bottom: 0.18rem;
  text-decoration: underline;
  cursor: pointer;
}
