.downloader {
  width: 100%;
  line-height: 1.5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: -0.1rem 0;
}
.downloader .progress {
  margin: 0 auto;
  margin-top: 0.2rem;
  width: 100%;
  display: flex;
  align-items: center;
}
.downloader .progress .percent {
  font-size: 0.16rem;
  margin-left: 0.1rem;
  width: 0.5rem;
}
.downloader .progress .bar {
  flex: 1;
  height: 0.2rem;
  border-radius: 0.2rem;
  background: #fff;
  overflow: hidden;
  background: #eee;
}
.downloader .progress .bar .bar-i {
  border-radius: 0.2rem;
  background: #39bffe;
  height: 100%;
  width: 0%;
}
.downloader .info {
  font-size: 0.2rem;
  text-align: center;
}
.downloader .info.notice {
  margin-top: 0.1rem;
  line-height: 1.5;
  font-size: 0.2rem;
  color: #666;
}
.downloader .restart-btn {
  width: 0.8rem;
  height: 0.32rem;
  font-size: 0.12rem;
  border-radius: 0.05rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(./7eb542954a900c3f9983e4e15e5f9015.png) no-repeat;
  background-size: 100% 100%;
  cursor: pointer;
  margin-top: 0.15rem;
}
.downloader .restart-btn:hover {
  opacity: 0.5;
}
